import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { EditorState, convertToRaw, ContentState } from 'draft-js';
import { Editor } from 'react-draft-wysiwyg';
import draftToHtml from 'draftjs-to-html';
import htmlToDraft from 'html-to-draftjs';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';

// https://jpuri.github.io/react-draft-wysiwyg/#/docs?_k=jjqinp

const WysiwygEditor = (props) => {
  const { getEditorContent, initialContent } = props;
  const contentBlock = htmlToDraft(
    initialContent || '<p>Please enter <strong>something</strong></p>'
  );
  let initialEditorState;
  if (contentBlock) {
    const contentState = ContentState.createFromBlockArray(
      contentBlock.contentBlocks
    );
    initialEditorState = EditorState.createWithContent(contentState);
  }
  const [editorState, setEditorState] = useState(initialEditorState);
  // const [content, setContent] = useState(draftToHtml(convertToRaw(editorState.getCurrentContent())));
  const onEditorStateChange = (editorState) => {
    setEditorState(editorState);
    getEditorContent(
      draftToHtml(convertToRaw(editorState.getCurrentContent()))
    );
  };

  return (
    <Editor
      // toolbarHidden
      // toolbarOnFocus
      // initialEditorState={editorState}
      editorState={editorState}
      wrapperClassName="wrapper-class"
      editorClassName="editor-class"
      toolbarClassName="toolbar-class"
      onEditorStateChange={onEditorStateChange}
      // onContentStateChange={onContentStateChange}
      // toolbar={{
      //   inline: { inDropdown: true },
      //   list: { inDropdown: true },
      //   textAlign: { inDropdown: true },
      //   link: { inDropdown: true },
      //   history: { inDropdown: true },
      // }}
    />

    // how to dispaly html content in react
    // {Parser().parse(draftToHtml(convertToRaw(editorState.getCurrentContent())))}
  );
};

WysiwygEditor.propTypes = {
  initialContent: PropTypes.string,
  getEditorContent: PropTypes.func
};

export default WysiwygEditor;
