import React from 'react';
import PropTypes from 'prop-types';

// @mui
import { AddTwoTone, ArrowBack } from '@mui/icons-material';
import {
  Breadcrumbs,
  Button,
  Grid,
  IconButton,
  Link,
  Paper,
  Stack,
  Tooltip,
  Typography
} from '@mui/material';
import { useTheme } from '@emotion/react';
import { useNavigate } from 'react-router-dom';

const PageHeader = (props) => {
  const theme = useTheme();
  const navigate = useNavigate();
  const { title, subtitle, breadcrumbs, back, control, action, ...rest } =
    props;
  return (
    <Paper
      variant="outlined"
      elevation={18}
      sx={{
        padding: theme.spacing(3),
        marginBottom: theme.spacing(3)
        // boxShadow:
        //     theme.palette.mode === 'dark'
        //         ? `0 1px 0   ${theme.palette.grey[200]}, 0px 2px 4px -3px rgba(0, 0, 0, 0.2), 0px 5px 12px -4px rgba(0, 0, 0, .1)`
        //         : `0px 2px 4px -3px ${theme.palette.grey[300]}, 0px 4px 8px -5px  ${theme.palette.grey[200]}`
      }}
    >
      <Grid
        container
        justifyContent="space-between"
        alignItems="center"
        {...rest}
      >
        <Grid item>
          {breadcrumbs && (
            <Breadcrumbs aria-label="breadcrumb">
              {breadcrumbs.map((breadcrumb, index) => (
                <Link
                  key={index}
                  underline="hover"
                  color="inherit"
                  pathname={breadcrumb.pathname}
                >
                  {breadcrumb.name}
                </Link>
              ))}
            </Breadcrumbs>
          )}
          <Stack
            direction="row"
            justifyContent="flex-start"
            alignItems="center"
          >
            {back && (
              <Tooltip arrow placement="top" title="Go back">
                <IconButton
                  color="primary"
                  aria-label="Back"
                  component="label"
                  onClick={() => window.history.back()}
                  sx={{ p: 2, mr: 2 }}
                >
                  <ArrowBack />
                </IconButton>
              </Tooltip>
            )}
            <Stack spacing={1}>
              <Typography
                variant="h3"
                component="h3"
                gutterBottom
                sx={{ fontWeight: 600 }}
              >
                {title}
              </Typography>
              {subtitle && (
                <Typography variant="subtitle2">{subtitle}</Typography>
              )}
            </Stack>
          </Stack>
        </Grid>
        <Grid item>
          {action}
          {control && (
            <Button
              variant="contained"
              startIcon={<AddTwoTone fontSize="small" />}
              onClick={() => navigate(control.route)}
            >
              {control.label}
            </Button>
          )}
        </Grid>
      </Grid>
    </Paper>
  );
};

PageHeader.propTypes = {
  title: PropTypes.string,
  subtitle: PropTypes.string,
  breadcrumbs: PropTypes.array,
  back: PropTypes.bool,
  control: PropTypes.object,
  action: PropTypes.node
};

export default PageHeader;
