import React from 'react';

export default function CustomizedImage({
  url,
  width = 600,
  height = 480,
  sx = {}
}) {
  return (
    <img
      src={url}
      alt=""
      style={{
        width: width || 600,
        height: height || 480,
        objectFit: 'cover',
        ...sx
      }}
    />
  );
}
